import { Component, OnInit } from '@angular/core';
import { EventService } from '../../../services/event.service';
import { Event } from '../../../models/event.model';
import { AuthService } from '../../../services/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-event-list',
  templateUrl: './event-list.component.html',
  styleUrls: ['./event-list.component.css']
})
export class EventListComponent implements OnInit {
  events: Event[] = [];
  loading = true;
  error: string | null = null;
  
  // isAdmin getter, ami egyszeren meghvja az authService.isAdmin() fggvnyt
  get isAdmin(): boolean {
    // Direkt debug informcik a hiba megoldshoz
    console.log('isAdmin getter hvsa, eredmny:', this.authService.isAdmin());
    return this.authService.isAdmin();
  }

  constructor(
    private eventService: EventService,
    public authService: AuthService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.loadEvents();
  }
  
  /**
   * Ellenrzi, hogy a felhasznl be van-e jelentkezve
   */
  isAuthenticated(): boolean {
    try {
      const userData = localStorage.getItem('currentUser');
      return !!userData; // Ha van felhasznli adat, akkor be van jelentkezve
    } catch (error) {
      console.error('Hiba a bejelentkezsi llapot ellenrzse kzben:', error);
      return false;
    }
  }

  /**
   * Ellenrzi, hogy a bejelentkezett felhasznl rendelkezik-e admin jogosultsggal
   */
  hasAdminRole(): boolean {
    // Ahelyett, hogy megprblnnk sajt logikt implementni, egyszeren hasznljuk az authService-t
    // gy ha a problma jelen van, akkor csak egy helyen kell javtani
    const isAdmin = this.authService.isAdmin();
    console.log('hasAdminRole eredmnye (authService.isAdmin()):', isAdmin);
    
    // Ha ez most false, akkor kzvetlenl prbljuk meg a token nlkl, a user objektum megtekintsvel
    if (!isAdmin) {
      try {
        const userDataStr = localStorage.getItem('user');
        if (userDataStr) {
          const userData = JSON.parse(userDataStr);
          console.log('User objektum a localStorage-bl:', userData);
          if (userData && userData.role === 'admin') {
            console.log('User objektum tartalmaz admin szerepkrt!');
            return true;
          }
        } else {
          console.log('Nincs user objektum a localStorage-ban');
        }
      } catch (e) {
        console.error('Hiba a user objektum feldolgozsa kzben:', e);
      }
    }
    
    return isAdmin;
  }

  loadEvents(): void {
    this.loading = true;
    this.eventService.getEvents().subscribe({
      next: (events) => {
        this.events = events;
        this.loading = false;
      },
      error: (err) => {
        this.error = 'Hiba trtnt az esemnyek betltse kzben.';
        this.loading = false;
        console.error('Error loading events:', err);
      }
    });
  }

  viewDetails(eventId: string): void {
    this.router.navigate(['/events', eventId]);
  }

  purchaseTicket(eventId: string): void {
    this.router.navigate(['/events', eventId, 'purchase']);
  }

  editEvent(eventId: string): void {
    this.router.navigate(['/events', eventId, 'edit']);
  }



  /**
   * j esemny ltrehozshoz navigl az esemny rlapra
   */
  createEvent(): void {
    // Egyszer, kzvetlen navigci az j esemny rlapra
    this.router.navigate(['/events/create']);
  }
} 