import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormArray } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { EventService } from '../../../services/event.service';
import { Event, NewEvent, TicketCategory } from '../../../models/event.model';

@Component({
  selector: 'app-event-form',
  templateUrl: './event-form.component.html',
  styleUrls: ['./event-form.component.css']
})
export class EventFormComponent implements OnInit {
  eventForm: FormGroup;
  isEditMode = false;
  eventId: string | null = null;
  loading = false;
  error: string | null = null;

  constructor(
    private fb: FormBuilder,
    private eventService: EventService,
    private route: ActivatedRoute,
    public router: Router
  ) {
    this.eventForm = this.fb.group({
      title: ['', Validators.required],
      description: ['', Validators.required],
      date: ['', Validators.required],
      location: ['', Validators.required],
      imageUrl: [''],
      ticketCategories: this.fb.array([])
    });
  }

  ngOnInit(): void {
    this.eventId = this.route.snapshot.paramMap.get('id');
    if (this.eventId) {
      this.isEditMode = true;
      this.loadEvent(this.eventId);
    }
  }

  get ticketCategories(): FormArray {
    return this.eventForm.get('ticketCategories') as FormArray;
  }

  addTicketCategory(): void {
    this.ticketCategories.push(this.fb.group({
      name: ['', Validators.required],
      price: [0, [Validators.required, Validators.min(0)]],
      availableTickets: [0, [Validators.required, Validators.min(0)]]
    }));
  }

  removeTicketCategory(index: number): void {
    this.ticketCategories.removeAt(index);
  }

  loadEvent(id: string): void {
    this.loading = true;
    this.eventService.getEvent(id).subscribe({
      next: (event: Event) => {
        this.eventForm.patchValue({
          title: event.title,
          description: event.description,
          date: new Date(event.date).toISOString().split('T')[0],
          location: event.location,
          imageUrl: event.imageUrl
        });

        event.ticketCategories.forEach((category: TicketCategory) => {
          this.ticketCategories.push(this.fb.group({
            name: [category.name, Validators.required],
            price: [category.price, [Validators.required, Validators.min(0)]],
            availableTickets: [category.availableTickets, [Validators.required, Validators.min(0)]]
          }));
        });

        this.loading = false;
      },
      error: (error: any) => {
        this.error = 'Hiba trtnt az esemny betltse kzben.';
        this.loading = false;
        console.error('Error loading event:', error);
      }
    });
  }

  onSubmit(): void {
    if (this.eventForm.invalid) {
      return;
    }

    this.loading = true;
    
    // Az adatok elksztse a form-bl
    const formData = this.eventForm.value;
    
    // Biztostsuk, hogy legyen legalbb egy jegykategoria, ha nincs formban
    if (!formData.ticketCategories || formData.ticketCategories.length === 0) {
      this.addTicketCategory();
      formData.ticketCategories = [{
        name: 'Alap jegy',
        price: 1000,
        availableTickets: 100
      }];
    }
    
    // Kezeljk a hibahelyzetet, amikor a ticketCategories tmb, de res
    if (Array.isArray(formData.ticketCategories) && formData.ticketCategories.length === 0) {
      formData.ticketCategories = [{
        name: 'Alap jegy',
        price: 1000,
        availableTickets: 100
      }];
    }
    
    // Szkges formzsok s kiegsztsek
    const newEventData: NewEvent = {
      title: formData.title,
      description: formData.description,
      date: new Date(formData.date).toISOString(),
      location: formData.location,
      imageUrl: formData.imageUrl || '',
      ticketCategories: formData.ticketCategories,
      availableTickets: this.calculateTotalAvailableTickets(formData.ticketCategories)
    };
    
    console.log('Elkldtt esemny adatok:', newEventData);
    
    // Ltrehozs vagy frissts az edit mode alapjn
    if (this.isEditMode && this.eventId) {
      // ID-t is megadunk az updateEvent-hez, mivel az mr ltez esemny (Event tpus, nem NewEvent)
      const existingEvent = {
        _id: this.eventId,
        ...newEventData
      };
      this.eventService.updateEvent(this.eventId, existingEvent).subscribe({
        next: () => {
          this.router.navigate(['/events', this.eventId]);
        },
        error: (error: any) => {
          this.error = 'Hiba trtnt az esemny frisstse kzben.';
          this.loading = false;
          console.error('Error updating event:', error);
        }
      });
    } else {
      this.eventService.createEvent(newEventData).subscribe({
        next: (event: Event) => {
          this.router.navigate(['/events', event._id]);
        },
        error: (error: any) => {
          this.error = 'Hiba trtnt az esemny ltrehozsa kzben.';
          this.loading = false;
          console.error('Error creating event:', error);
          
          // Teljes hiba megjelentse debug clbl
          if (error.error) {
            console.log('Szerver hiba rszletei:', error.error);
            if (error.error.message) {
              console.log('Szerver hibazenet:', error.error.message);
              this.error = `Szerver hiba: ${error.error.message}`;
            }
          }
          
          // Utols mentsvarknt prbljuk meg string formtumra konvertlni a JSON objektumot
          try {
            const requestBody = JSON.stringify(newEventData);
            console.log('Elkldtt adatok stringknt:', requestBody);
          } catch (e) {
            console.error('Hiba a JSON stringgels sorn:', e);
          }
        }
      });
    }
  }
  
  /**
   * Kiszmtja az sszes elrhet jegy szmt az sszes kategribl
   */
  private calculateTotalAvailableTickets(categories: any[]): number {
    if (!categories || categories.length === 0) {
      return 0;
    }
    
    return categories.reduce((total, category) => {
      return total + (parseInt(category.availableTickets) || 0);
    }, 0);
  }
} 