import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { Ticket } from '../models/ticket.model';

@Injectable({
  providedIn: 'root'
})
export class TicketService {
  private apiUrl = `${environment.apiUrl}/tickets`;

  constructor(private http: HttpClient) {}

  purchaseTicket(eventId: string, categoryName: string): Observable<Ticket> {
    return this.http.post<Ticket>(`${environment.apiUrl}/api/tickets`, {
      eventId,
      categoryName
    });
  }

  getUserTickets(): Observable<Ticket[]> {
    return this.http.get<Ticket[]>(`${this.apiUrl}/user`);
  }

  cancelTicket(ticketId: string): Observable<void> {
    return this.http.post<void>(`${this.apiUrl}/${ticketId}/cancel`, {});
  }

  getEventTickets(eventId: string): Observable<Ticket[]> {
    return this.http.get<Ticket[]>(`${environment.apiUrl}/api/tickets/event/${eventId}`);
  }
} 